@use 'global' as *;



.contact__inner {
    max-width: rem(600);
    position: relative;
}

.contact__inner::before {
    content: "";
    position: absolute;
    width: rem(109);
    height: rem(50);
    background-image: url(../images/common/fish-left.svg);
    background-repeat: no-repeat;
    background-size: contain;
    top: -4%;
    right: 0;
    @include mq("md"){
        width: rem(301);
        height: rem(138);
        top: 0;
        right: calc(50% - min(50vw, rem(670)));
    }
}


.contact__contents {
    text-align: center;
    @include mq("md"){
        display: flex;
        box-shadow: rem(0) rem(4) rem(4) rgba($color: #000, $alpha: 0.25);
        padding: rem(80) 0;
        align-items: center;
    }
}

.contact__map {
    @include mq("md"){
        border-right: 1px solid $green-accent;
        margin-inline: auto;
        width: 50%;
        padding: 0 rem(50);
    }
}

.contact__logo-wrap {
    border-bottom: 1px solid $green-accent;
}

.contact__logo-wrap img {
    display: inline-block;
    text-align: center;
}

.contact__logo {
    width: rem(174);
    @include mq("md"){
        width: rem(205);
    }
}

.contact__text-wrap {
    margin-top: rem(16);
    @include mq("md"){
        margin-top: rem(24);
        display: flex;
        gap: rem(21);
        justify-content: space-between;
    }
}

.contact__text {
    @include mq("md"){
        text-align: left;
    }
}

.contact__text p {
    line-height: 1.5;
    font-size: rem(16);
    color: $green;
    text-shadow: rem(0) rem(4) rem(4) rgba($color: #000, $alpha: 0.25);
}

.contact__text p:not(:first-child) {
    margin-top: rem(4);
}

.contact__text-wrap iframe {
    margin-top: rem(16);
    aspect-ratio: 295 / 160;
    @include mq("md"){
        aspect-ratio: 273 / 148;
        margin-top: 0;
        max-width: rem(273);
    }
}

.contact__title-wrap {
    margin-top: rem(40);
    @include mq("md"){
        margin-top: 0;
        margin-inline: auto;
        width: 50%;
    }
}

.contact__button {
    text-align: center;
    margin-top: rem(40);
}