@use 'global' as *;

.gallery__inner {
    max-width: rem(600);
    @include mq("md"){
        position: relative;
    }
}

.gallery__inner::before {
    @include mq("md"){
        content: "";
        position: absolute;
        background-image: url(../images/common/fish-left.svg);
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(301);
        height: rem(138);
        top: -5%;
        right: calc(50% - min(50vw, 39.75rem));
    }
}

.gallery__contents {
    margin-top: rem(40);
    @include mq("md"){
        display: grid;
        margin-top: rem(75);
        gap: rem(5);
        grid-auto-flow: dense;
        grid-template-columns: repeat(11, 1fr);
        grid-auto-rows: 1fr;
    }
}

.gallery__img {
    display: block;
    margin-top: rem(8);
    cursor: pointer;
    @include mq("md"){
        margin-top: 0;
        aspect-ratio: 580 / 369;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}


.gallery__img:first-child {
    @include mq("md"){
        aspect-ratio: 746 / 492;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.grid__img:first-child {
    @include mq("md"){
        grid-column: span 5;
        grid-row: span 2;
    }
}

.gallery__img:nth-child(6n+1) {
    @include mq("md"){
        grid-column: span 5;
        grid-row: span 2;
    }
}

.gallery__img:nth-child(6n+2),
.gallery__img:nth-child(6n+3) {
    @include mq("md"){
        grid-column: span 6;
        grid-row: span 1;
    }
}

.gallery__img:nth-child(6n+4) {
    @include mq("md"){
        grid-column: span 6;
        grid-row: span 1;
    }
}

.gallery__img:nth-child(6n+5) {
    @include mq("md"){
        grid-column: span 6;
        grid-row: span 1;
    }
}

.gallery__img:nth-child(6n+6) {
    @include mq("md"){
        grid-column: span 5;
        grid-row: span 2;
    }
}

#grayDisplay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    background: rgba(32, 32, 32, 0.8);
    z-index: 9999;

}

#grayDisplay img {
    width: 80%;
    height: 80%;
    object-fit: contain;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
}
